.site-footer {
    display: flex;
    flex-direction: column;
    align-content: space-around;
    align-items: center;
    justify-content: space-around;
    border-top: 1px solid var(--color__background__alt);
    font-family: var(--font__meta);
}

.site-footer__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: var(--max-width__base);
    padding: var(--padding__large) var(--padding__medium);
    font-size: var(--font__size-base);
    text-align: center;

    label {
        color: var(--color__text);
    }
}

.site-footer__navigation {
    color: var(--color__text);

    a {
        color: var(--color__text);
        &:visited {
            color: var(--color__text);
        }
    }
}

.site-footer__heading {
    margin-top: 0;
    margin-bottom: var(--margin__small);
    font-size: var(--font__size__heading-5);
    font-family: var(--font__meta);
}
